


















import {
  defineComponent,
  reactive,
  computed,
  toRefs
} from '@vue/composition-api';
import { standardField, requiredField, uniqueField } from '@/use/validations';

export default defineComponent({
  name: 'GamesAddDialog',
  setup(_, { root: { $store } }) {
    const gamesNames = computed(() => $store.getters['games/getGamesNames']());

    const checkUnique = (v: string) => uniqueField(v, gamesNames.value, true);

    const state = reactive({
      name: '',
      coop: false,
      nameRules: [...standardField, requiredField, checkUnique]
    });

    const createGame = () => {
      const game = {
        coop: state.coop,
        name: state.name
      };
      $store.dispatch('games/createGame', game);
    };

    const submitGame = () => {
      createGame();
      state.name = '';
    };

    return {
      submitGame,
      ...toRefs(state)
    };
  }
});
